<template>
  <div class="box-son" v-show="getsum">
    <input type="checkbox" @change="selectAll($event)" :checked="isflg" />
    <span>已完成{{ getcount }}/全部{{ getsum }}</span>
    <button @click="clearClick">清除已完成任务</button>
  </div>
</template>

<script>
import { mapMutations, mapActions, mapState } from "vuex";
export default {
  data() {
    return {};
  },
  methods: {
    ...mapMutations(["clearClick", "selectAll"]),
    // 清除选中的任务
    // clearClick() {
    //   this.$store.commit("clearClick");
    // },
    // 是否全选
    // selectAll(e) {
    //   this.$store.commit("selectAll", e.target.checked);
    // },
  },
  computed: {
    ...mapState(["dataInfo"]),
    // 获取已完成的个数
    getcount() {
      let cont = this.$store.state.dataInfo.reduce(function (num, item) {
        return item.done ? num + 1 : num;
      }, 0);
      return cont;
    },
    // 获取全部个数
    getsum() {
      return this.dataInfo.length;
    },
    // 判断全选按钮是否选中
    isflg() {
      return this.getcount == this.getsum;
    },
  },
};
</script>

<style scoped>
.box-son {
  height: 40px;
  line-height: 50px;
  /* background-color: red; */
}
span {
  margin-left: 10px;
}
button {
  float: right;
  margin-top: 10px;
  height: 30px;
}
</style>