<template>
  <div>
    <input type="text" placeholder="请输入" v-model="val" @keydown="addList" />
  </div>
</template>

<script>
// 导入插件nanoid
// import { nanoid } from "nanoid";
// import { mapActions } from "vuex";
export default {
  data() {
    return {
      val: "",
    };
  },
  methods: {
    // 事件发生时给vuex发送一条数据
    addList(e) {
      if (e.key === "Enter") {
        this.$store.dispatch("addList", this.val);
        e.target.value = "";
      }
    },
  },
};
</script>

<style scoped>
input {
  width: 100%;
  height: 30px;
}
</style>