export default {
  // 添加数据
  addList(context, val) {
    if (val.trim() === '') return
    context.commit('addList', val)
  },
  // 删除数据
  delLis(context, id) {
    // context.state.dataInfo.id===id
    let newdata = []
    newdata = context.state.dataInfo.forEach(function (item, idx) {
      if (item.id === id) {
        context.commit('delLis', idx)
      }
    })
  },
  // 编辑数据
  newtsk(context, newdata) {
    context.state.dataInfo.forEach(function (item, idx) {
      if (item.id === newdata.id) {
        context.commit('newtsk', { value: newdata.value, idx })
      }
    })
  }

}