<template>
  <div><ListItem v-for="item in getdata" :key="item.id" :item="item" /></div>
</template>

<script>
import ListItem from "./ListItem.vue";
export default {
  components: {
    ListItem,
  },
  computed: {
    getdata() {
      // 从vuex获取公共数据
      return this.$store.state.dataInfo;
    },
  },
  // 监视vuex的数据变化 并存入本地
  watch: {
    getdata: {
      deep: true,
      handler(newval) {
        localStorage.setItem("datas", JSON.stringify(newval));
        // console.log(newval);
      },
    },
  },
};
</script>

<style>
.v-enter-active {
  animation: goto 1s;
}
.v-leave-active {
  animation: goto 0.6s reverse;
}
@keyframes goto {
  0% {
    transform: translate(-100%, 0);
  }
  100% {
    transform: translate(0, 0);
  }
}
</style>