<template>
  <div class="box">
    <input type="checkbox" v-model="isSelected" @click="getiselect" />
    <span v-show="!isblock">{{ item.task }}</span>
    <input
      v-show="isblock"
      type="text"
      v-model="newTask"
      ref="inp"
      @blur="isshow"
      @keydown="keyisShow"
    />
    <button @click="delList(item.id)">删除</button>
    <button @click="isedit" v-show="!isblock">编辑</button>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  props: ["item"],
  data() {
    return {
      newTask: this.item.task,
      isblock: false,
    };
  },
  computed: {
    // 获取vuex中的是否选中
    isSelected: {
      get() {
        return this.item.done;
      },
      set(value) {
        this.item.done = value;
      },
    },
  },
  methods: {
    ...mapActions({ delList: "delLis" }),
    // 点击删除数据
    // delList(id) {
    //   this.$store.dispatch("delLis", id);
    // },
    // 显示隐藏编辑区
    isedit() {
      this.isblock = true;
      this.$nextTick(function () {
        this.$refs.inp.focus();
      });
    },
    // 失去焦点时隐藏
    isshow() {
      this.isblock = false;
    },
    keyisShow(e) {
      if (e.key === "Enter") {
        this.isblock = false;
      }
    },
    // 选中或未选中的状态传给vuex
    getiselect() {
      this.$store.commit("getiselect", {
        isSelect: !this.item.done,
        id: this.item.id,
      });
    },
  },
  watch: {
    // 编辑数据
    newTask(newtsk) {
      this.$store.dispatch({
        type: "newtsk",
        value: newtsk,
        id: this.item.id,
      });
    },
  },
};
</script>

<style scoped>
.box {
  margin: 5px 0;
  background-color: rgb(187, 161, 161);
}
span {
  display: inline-block;
  margin-left: 10px;
  /* width: 290px; */
}
button {
  float: right;
}
</style>