
import { nanoid } from 'nanoid';
export default {
  // 添加数据
  addList({ dataInfo }, val) {
    dataInfo.unshift({ id: nanoid(), done: false, task: val })
    window.localStorage.setItem('datas', JSON.stringify({ id: nanoid(), done: false, task: val }))
  },
  // 删除数据
  delLis(state, idx) {
    state.dataInfo.splice(idx, 1)
  },
  // 编辑数据
  newtsk(state, value) {
    state.dataInfo[value.idx].task = value.value
  },
  // 获取选中状态
  getiselect(state, value) {
    state.dataInfo.forEach(function (item) {
      if (item.id === value.id) {
        item.done = value.isSelect
      }
    })
    console.log(value);
  },
  // 清除选中的任务
  clearClick(state) {
    state.dataInfo = state.dataInfo.filter(function (item) {
      return !item.done
    })
  },
  // 是否全选
  selectAll(state, e) {
    state.dataInfo.forEach(function (item) {
      item.done = e.target.checked
    })
  }
}